.header--navigation .navigation-list {
    display: flex;
    gap: 1.5em;

    &__links {
        text-decoration: none;
        color: var(--clr-neutral-100);
        font-size: var(--fs-300);

        &:hover {
            text-decoration: underline;
            font-weight: var(--fw-bold);
        }
    }
}